
import listItem from '@/components/listItem'
import ShopBtn from '@/components/shopBtn'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'


import { getOrderDetail, cancelOrder, deleteOrder, ConfirmOrder, BuyOrderAgain, submitProductSNcode ,ApplyServiceOrder} from '@/api/user'
export default {
name: 'uc-orderDetail',
components: {
listItem,
ShopBtn,
Confirm,
XButton,
Checklist
},
data () {
return {
orderDetail: {addressinfo: {name: '', mob: '', address: ''}, invoiceinfo: {duty: '', title: ''}, productinfo: [], priceinfo: {productmoney: '', activemoney: '', pointmoney: '', ticketmoney: '', freightmoney: '', paymoney: ''}, more: {str1: '', str2: '', money1: '', money2: '', moneypay2: ''}},
goodsData: [
{
img: '',
titL: '',
titR: '',
subtitL: '',
subtitR: ''
}
],
snList: [1, 2],
snCheck: [], // 默认选中第一个
snShow: false
}
},
filters: {
format: function (val) {
const str1 = val.substring(0, 3)
const str2 = val.substring(7, 11)
const str3 = str1 + '****' + str2
return str3
},
statusFilter (val) {
let res = ''
switch (val) {
case 'waittingpay':
res = '待付款'
break
case 'payed':
res = '已付款'
break
case 'payedone':
res = '已付定金'
break
case 'waittingsecpay':
res = '待付尾款'
break
case 'sent':
res = '已发货'
break
case 'recieve':
res = '已签收'
break
case 'finish':
res = '已完成'
break
case 'refunding':
res = '退款中'
break
case 'agreerefund':
res = '同意退款'
break
case 'reject':
res = '拒绝退款'
break
case 'waittingconfirm':
res = '退款待确认'
break
case 'refunded':
res = '退款完成'
break
case 'overtime':
res = '超时关闭'
break
case 'cancel':
res = '取消关闭'
break
default:
res = '未知'
break
}
return res
}
},

created () {
this.getOrderDetail()
},
methods: {
allconfirm (item,stauas) {
var contenttxt = '';
const _that = this
if(stauas==='recieve'){
contenttxt ="确认收货?"
}
else if(stauas==='waittingpay'){
contenttxt ="确认取消此订单?"
}
else if(stauas==='cancel'){
contenttxt ="确认删除此订单?"
}
else if(stauas==='finish'){
contenttxt ="确认发起售后申请吗?"
}
this.$vux.confirm.show({
title: '温馨提示',
content: contenttxt,

onCancel () {
},
onConfirm () {
_that.$vux.loading.show()
if(stauas==='recieve'){
_that.handleConfirm(item)
}
else if(stauas==='waittingpay'){
_that.handleCancel(item)
}
else if(stauas==='cancel'){
_that.handleDel(item)
}
else if(stauas==='finish'){
_that.handleApply(item)
}
}
})
},
getOrderDetail () {

const id = this.$route.query.id
console.log(id)
const postData = {
orderid: id
}
getOrderDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {

if (window.location.href.indexOf('&showtips=1') !== -1) {
this.$vux.confirm.show({
title: '',
content: '请确认支付是否已完成',
confirmText: '已完成',
cancelText: '未完成',
onCancel () {
window.location.href = window.location.href.split('&showtips=1')[0]
},
onConfirm () {
window.location.href = window.location.href.split('&showtips=1')[0]
}
})
}

console.log(res.data)
this.orderDetail = res.data.data
const that = this
that.goodsData = []
res.data.data.productinfo.map(item => {
that.snList = that.snList.concat(item.snlist)
let newObj = {
img: item.skuimg,
titL: item.productname,
titR: '￥' + item.price,
subtitL: item.skuname,
subtitR: '×' + item.num
}
that.goodsData.push(newObj)
})
if (that.snList.length > 0) that.snCheck.push(that.snList[0])
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleCancel (item) {
console.log(item)
const postData = {
orderid: item.orderid
}
cancelOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('取消成功', 'middle')
this.getOrderDetail()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleDel (item) {
const postData = {
orderid: item.orderid
}
deleteOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('删除成功', 'middle')
this.$router.push({
name: 'myOrder'
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleApply (item) {
const postData = {
orderid: item.orderid
}
ApplyServiceOrder(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('申请成功，请等待客服审核', 'middle')
this.$router.push({
name: 'myOrder'
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

buyAgain (item) {
const postData = {
orderid: item.orderid
}
BuyOrderAgain(postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$router.push({path: '/confirmOrder', query: { preid: res.data.data.preid}})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handlePay (item) {
window.location.href = process.env.BASE_SELF + '/wap/index.html#/nowPay?id=' + item.orderid

/*this.$router.push({
name: 'nowPay',
query: {
id: item.orderid
}
})*/
},

handleConfirm (item) {
const postData = {
orderid: item.orderid
}
ConfirmOrder(postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$vux.toast.text('操作成功', 'middle')

this.getOrderDetail()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleComment (item) {
this.$router.push({
name: 'uc-submitcomments',
query: {
id: item.orderid
}
})
},

handleLogistics (item) {
this.$router.push({
name: 'uc-orderLogistics',
query: {
id: item.orderid
}
})
},

handleDetail (row) {
console.log(row)
this.$router.push({
name: 'uc-orderDetail',
query: {
id: row.orderid
}
})
},

handleRefundProgress (item) {
this.$router.push({
name: 'uc-refundDetail',
query: {
id: item.orderid
}
})
},

handleRefund (item) {
this.$router.push({
name: 'uc-submitRefunds',
query: {
id: item.orderid
}
})
},
handleSN () {
if (this.snCheck.length < 1) {
this.$vux.toast.text('请选择一个设备SN码', 'middle')
return false
} else {
let that = this
that.$vux.confirm.show({

content: '确认领取？',

confirmText: '确认',
cancelText: '取消',

onCancel () {

},
onConfirm () {
const postData = {
orderid: that.orderDetail.orderid,
sncode: that.snCheck[0]
}
submitProductSNcode(postData)
.then(res => {
that.$vux.loading.hide()
if (res.data.errorcode === 0) {

that.$vux.toast.text('领取成功', 'middle')

that.getOrderDetail()
} else {
that.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
that.$vux.loading.hide()
console.log(err)
that.$vux.toast.text('网络异常！', 'middle')
})
}
})
}
}
}
}



